.navbar {
  overflow: hidden;
  list-style-type: none;
  background-color: rgb(68, 68, 68);
  margin-top: -10px;
}

.nav-menu {
  align-items: center;
  justify-content: center;
  list-style: none;
  text-align: center;
  justify-content: end;
}
.navbar-container{
  display: flex;
  justify-content: space-between;
  height: 50px;
  
}
.navbar-logo {
  color: #fff;
  font-family: 'Trebuchet MS', sans-serif;
  font-size: 20px;
  margin-top: 15px;
}
.faBars {
  color: #fff;
}
.menu-icon {
  display: none;
  margin-top: -10px;
}
.nav-item {
  list-style-type: none;
  float: left;
}
.nav-links {
  color: #fff;
  display: flex;
  text-decoration: none;
  margin-top: 15px;
  padding: 8px 15px;
  height: 100%;
}
@media only screen and (max-width: 960px) {
  .navbar {
      position:flex;
    }

.menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(100%, 40%);
      font-size: 1.8rem;
      cursor: pointer;
      color: #fff;
}    
.nav-menu.active {
  display: flex;
  background: rgb(68, 68, 68);
  right: 0;
  opacity: 1;
  z-index: 0;
  width: 100%;
}
.nav-links {
  text-align: center;
  padding: 2rem;
  width: 100%;
  display: table;
}

.menu-icon {
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(-100%, 60%);
  font-size: 1.8rem;
  cursor: pointer;
}
.fa-Times {
  color: #fff;
  font-size: 2rem;
}
.nav-menu {
  background-color: #060b26;
  width: 100%;
  height: 140vh;
  display: flex;
  flex-direction: column;
  position:absolute;
  top: 40px;
  right: 100%;
  transition: 850ms;
  animation-direction: reverse;
}
.nav-item {
  list-style-type: none;
  align-items: center;
  display: flex;
}
.nav-links {
  text-align: center;
  padding: 2rem;
  width: 100%;
  top: 200px;
}
.nav-item:hover {
  border: none;
}


}