.Contact {
    text-align: center;
    background-color: #06db8a;
      height: 100%;

  }
  .Title{
   font-size: 75px;
   color:white;
   animation-duration: .75s;
   animation-name: slidein;
   margin: 0;
 }
 .box{
  background-color: lightgrey;
  width: 300px;
  border-radius: 25px;
  border: 15px solid rgb(2, 156, 2);
  margin: auto;
  animation: fadeIn 1.5s;
    
}

@keyframes fadeIn {
  0% { opacity: 0; }
  100% { opacity: 1; }
  }

 .icons  {
  display:flex;
  justify-content:center;
  align-items: center;
  animation-name: slidein-left;
 }
 .social-icon-link {
   margin-right: 5px;
   list-style-type: none;
   align-items: center;
   color: rgb(0, 180, 54);
 }
 .social-icon-link{
    text-align: center;
    transition: 850ms;
    animation-duration: 1.5s;
 }
 @keyframes slidein {
   from {
     margin-left: 100%;
     width: 300%;
   }
   to {
     margin-left: 0%;
     width: 100%;
   }

 }
 @keyframes slidein-left {
  from {
    margin-right: 100%;
    width: 300%;
  }
  to {
    margin-right: 0%;
    width: 100%;
  }
}