.App {
  background-color: #06db8a;
  height: 100%;
}

.Home {
    text-align: center;
    background-color: #06db8a;
    min-height: 100%
  }
  .HomeIcon{
    font-size: 100px;
    width: 300px;
    color: rgb(0, 180, 54);
    animation: floating 2s infinite;
  }

  @keyframes floating {
    0% { transform: translate(0,  0px); }
    50%  { transform: translate(0, 15px); }
    100%   { transform: translate(0, -0px); }   
}
  .Home-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  .initial-header {
    padding: 80px;
    color: white;
    margin: 0;
    animation: fadeIn 1s;
    font-family: cursive;
    font-size: 120px;
  }

  @keyframes fadeIn {
    0% { opacity: 0; }
    100% { opacity: 1; }
    }

  .navbar a {
    float: left;
    display: block;
    color: white;
    text-align: center;
    padding: 8px 15px;
    text-decoration: none;
  }
  .navbar a:hover { color: #00FF00;}
  .navbar a.right {
    float: right;
  }
  .Home-header {
    background-color: #8b0e0e94;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  .home-text{
    color: white;
    font-size: 30px;
    font-family: 'Courier New', Courier, monospace;
    padding-top: 10px;
    padding-bottom: 10px;
    margin: 10px;
    animation: fadeIn 1s;
  }
  
  
  .Home-link {
    color: #61dafb;
  }
  
  