.Projects {
    text-align: center;
    background-color: #06db8a;
  }
  
  .Home-logo {
    height: 40vmin;
    pointer-events: none;
  }

  .button-header{
    font-size: 0.7em;
  }


  .project-header {
    padding: 80px;
    color: white;
    margin: 0;
    animation: fadeIn 1s;
    font-family: 'Courier New', Courier, monospace;
    font-size: 100px;
    
  }

  @keyframes fadeIn {
    0% { opacity: 0; }
    100% { opacity: 1; }
    }
    
  .navbar a {
    float: left;
    display: block;
    color: white;
    text-align: center;
    padding: 8px 15px;
    text-decoration: none;
  }
  .navbar a:hover { color: #00FF00;}
  .navbar a.right {
    float: right;
  }
  .bottom-header {
    background-color: #00776394;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .Home-link {
    color: #61dafb;
  }
